import React, { Component } from 'react';
import { Paper } from '@material-ui/core';
import FilterContainer from '../FilterContainer/FilterContainer';
import ResultsContainer from '../ResultsContainer';

import './MainContainer.css';

export default class MainContainer extends Component {

    state = {
        page: 'filter',
        results: [],
        resultTitle: ''
    }

    handleResults = (results, resultTitle) => {
        this.setState({ results });
        this.setState({ resultTitle });
        this.setState({ page: 'results' });
    }

    handleReturn = () => {
        this.setState({ page: 'filter' });
        this.setState({ resultTitle: '' });
        const results = [...this.state.results];
        results.length = 0;
        this.setState({ results });
    }

    componentDidMount() {

    }

    render() {
        const { page, results, resultTitle } = this.state;
        let renderPage;

        switch (page) {
            case 'results':
                renderPage = <ResultsContainer resultTitle={resultTitle} handleReturn={this.handleReturn} clinics={results} />
                break;
            case 'filter':
                renderPage = <FilterContainer handleResults={this.handleResults} />
                break;
            default: break;
        }

        return (
            <div className="main-container">

                <Paper className='paper'>
                    {renderPage}
                </Paper>

            </div>
        )
    }
}
